uch kling, es stimmt. Weiterbildung kostet. Und das oft nicht wenig. Die Preisspanne kann von ein paar Hundert bis zu ein paar Tausend Euro reichen. Dein Geld ist zwar gut in Dich angelegt, aber nicht jeder hat höhere Summen locker in der Brieftaschen liegen, um sich seine Weiterbildung zu finanzieren. ")]),_c('p',[_vm._v(" Allerdings, es gibt Hoffnung damit dein Traum von einem besseren Job nicht an dem mangelndem Kleingeld zerplatzen soll. Viele Seiten bieten finanzielle Unterstützung in unterschiedlicher Form an, die Du in Anspruch nehmen kannst. Lass Dich also von hohen Kursgebühren nicht abschrecken. Wenn Du weißt, welche finanziellen Förderungsmöglichkeiten Du ausschöpfen kannst, kann sich die Weiterbildung für Dich auch im wahrsten Sinne des Wortes auszahlen. ")]),_c('b-img-lazy',{staticClass:"border-radius1 shadow1 mb-4",attrs:{"srcset":"\n        /img/MfaMalAnders_FoerderungUndFinanzierungVonWeiterbildungen_gwmelv_c_scale,w_350.jpg 350w,\n        /img/MfaMalAnders_FoerderungUndFinanzierungVonWeiterbildungen_gwmelv_c_scale,w_785.jpg 785w,\n        /img/MfaMalAnders_FoerderungUndFinanzierungVonWeiterbildungen_gwmelv_c_scale,w_1200.jpg 1200w\n      ","src":"/img/MfaMalAnders_FoerderungUndFinanzierungVonWeiterbildungen_gwmelv_c_scale,w_1200.jpg","sizes":"(max-width: 768px) 100vw, 50vw","alt":"Was sind die Unterschiede zwischen Fortbildung, Weiterbildung und\n      Studium für MFA & ZFA?","width":"1200","height":"630","blank-width":"1200","blank-height":"630","blank-color":"#f7f6f9","offset":"1000","fluid":""}}),_c('p',{staticClass:"h3 bold"},[_vm._v(" Welche Möglichkeit hast Du als MFA & ZFA, um deine Weiterbildung zu finanzieren? ")]),_c('h3',{attrs:{"id":"chef-fragen"}},[_vm._v("Beim Chef nachfragen")]),_c('p',[_vm._v(" Das ist in vielen Situationen die einfachste und unkomplizierteste Lösung. Es kommt dabei aber auf die Art deiner "),_c('b-link',{attrs:{"to":"/karriere/fort-und-weiterbildung/ueberblick"}},[_vm._v("Weiterbildung")]),_vm._v(" an. Angenommen, D