._m(4),_c('h3',{attrs:{"id":"bildungsgutschein"}},[_vm._v("Bildungsgutschein")]),_vm._m(5),_vm._m(6),_vm._m(7),_c('h3',{attrs:{"id":"bildungsprämie"}},[_vm._v(" Prämiengutschein (Bildungsprämie) ")]),_vm._m(8),_vm._m(9),_c('p',[_vm._v(" Die berufliche Weiterbildung darf nicht im Zusammenhang mit einer betrieblichen Schulung stattfinden. Also zum Beispiel kann er nicht angewendet werden für eine Hygieneschulung, die in der Praxis stattfindet. ")]),_c('p',[_vm._v(" Der Prämiengutschein kann jährlich neu beantragt werden. So könntest du Dir jedes Jahr eine Weiterbildung mithilfe des Prämiengutscheins finanzieren. ")]),_vm._m(10),_c('h3',{attrs:{"id":"bildungskredit"}},[_vm._v("Bildungskredit")]),_c('p',[_vm._v(" Der Bildungskredit unterstützt bei schulischen Ausbildungen und bei einem Studium. ")]),_vm._m(11),_vm._m(12),_c('h3',{attrs:{"id":"aufstiegsstipendium"}},[_vm._v("Aufstiegsstipendium")]),_c('p',[_vm._v(" Warst Du eine besonders gute Schülerin während der Ausbildung? Und bist immer noch besonders motiviert und möchtest Dich beruflich durch ein Studium weiterqualifizieren? Dann könnte das Aufstiegsstipendium des Bundesministeriums für Bildung und Forschung für Dich in Frage kommen. Es richtet sich an Fachkräfte, die nach der Ausbildung bereits mindestens zwei Jahre Berufserfahrung haben. ")]),_c('p',[_vm._v(" Voraussetzungen dafür ist ein Nachweis über sehr gute Leistungen in Ausbildung und Beruf, u.a. durch die Note der Berufsabschlussprüfung oder der Abschlussprüfung einer Aufstiegsfortbildung (Gesamtergebnis mit mindestens Note 1,9 oder 87 Punkte und mehr). Eine weitere Möglichkeit ist die besonders erfolgreiche Teilnahme an einem überregionalen beruflichen Leistungswettbewerb (Platzierung unter den ersten drei). Sollten die ersten beiden Voraussetzungen für Dich nicht zutreffen kann auch ein begründeter Vorschlag des Arbeitgebers für die Bewerbung um ein Aufstiegsstipendium qualifizieren. ")]),_vm._m(13),_c('h3',{attrs:{"id":"weiterbildungsstipendium"}},[_vm._v(" Weiterbildungsstipendium ")]),_c('p',[_vm._v(" Das Weiterbildungsstipendium ist ähnlich wie das Aufstiegsstipendium. Es richtet sich an Berufstätige, die mithilfe einer Weiterbildung ihre Fertigkeiten und Kenntnisse spezialisieren wollen. Die Weiterbildung kann fachlich sein, also auf deinen Beruf aufbauen, wie z. B. der Fachwirt oder aber fachübergreifend. Damit sind beispielsweise berufsbegleitende Studiengänge gemeint. ")]),_c('p',[_vm._v(" Die Voraussetzungen sind die gleichen wie bei dem Aufstiegsstipendium. Allerdings muss man bei Aufnahme in das Programm unter 25 Jahre sein. Die Bewerbung richtest Du in dem Fall an die Ärztekammer, bei die damals deinen Ausbildungsvertrag gegengezeichnet hat. ")]),_vm._m(14),_c('h3',{attrs:{"id":"steuererklaerung"}},[_vm._v(" Kostenerstattung über die Steuererklärung ")]),_c('p',[_vm._v(" Die Kosten für eine Weiterbildung kannst Du auch von der Steuer absetzen. Bis zu 1000 Euro erkennt das Finanzamt bei Arbeitnehmern pauschal als Werbungskosten an. Wie hoch deine Rückzahlung ausfällt, hängt davon ab ,wie das Finanzamt deine Weiterbildung einordnet. Du kannst die konkret zuordenbaren Seminar- bzw. Studiengebühren, die Kosten für notwendige Literatur und Arbeitsmaterialien sowie Reisekosten (Fahrt, Übernachtung, Verpflegung etc.) geltend machen. ")]),_c('h3',{attrs:{"id":"qualifizierungsoffensive"}},[_vm._v(" WEITER.BILDUNG! – die Qualifizierungs­offensive ")]),_c('p',[_vm._v(" Die Bundesagentur für Arbeit hat eine Qualifizierungsoffensive ins Leben gerufen von der vor allem kleine Unternehmen mit weniger als 10 Mitarbeiter profitieren. Weiterbildungskosten werden hier sogar zu 100 % erstattet. ")]),_vm._m(15),_c('p',{staticClass:"mt-4"},[_vm._v(" Das waren 9 Möglichkeiten in der Kurzübersicht, die Dir eine finanzielle Hilfe sein können, um in deine Karriere als MFA oder ZFA zu investieren. Natürlich sind nicht für jeden die 9 Möglichkeiten der Finanzierung nutzbar, um deine Weiterbildung zu finanzieren. Dies soll für Dich lediglich ein Überblick über deine eventuellen Chancen auf die Reduzierung von Weiterbildungskosten sein. Welche Form der Förderung für Dich in Frage kommen könnte, findest Du heraus, wenn Du Dich bei den einzelnen Stellen direkt informierst. ")]),_c('p',[_vm._v(" Hast Du bereits Erfahrung mit einer der 9 Förderungen gemacht und kannst erzählen, wie das genau abgelaufen ist? Oder kennst Du noch eine andere Möglichkeit, die hier noch nicht aufgeführt ist? "),_c('b-link',{attrs:{"to":"/kontakt"}},[_vm._v("Lass es uns wissen")]),_vm._v(". ")],1),_vm._m(16),_c('p',[_vm._v(" Wusstest Du eigentlich, dass Du einen gesetzlichen Anspruch auf Weiterbildung während der Arbeitszeit hast? Dir stehen je nach Bundesland 5-10 Tage bezahlter Sonderurlaub zu. ")]),_c('p',[_vm._v(" Du kannst Dich von der Arbeit beurlauben lassen, um neue Erkenntnisse zu einem selbst gewählten Thema zu gewinnen. Es muss nicht zwingend eine Fortbildung im medizinischen Bereich sein. Du könntest z.B. auch einen Yogakurs oder Social Media Kurs machen oder eine neue Sprache lernen. ")]),_c('p',[_vm._v(" Das Gehalt wird während dieser Zeit vom Arbeitgeber weitergezahlt. ")]),_vm._m(17),_c('h2',{staticClass:"display-4 bold",attrs:{"id":"umschulung"}},[_vm._v(" Umschulung zur MFA & ZFA ")]),_c('p',[_vm._v(" Den Beruf der Medizinischen Fachangestellten | Zahnmedizinischen Fachangestellten erlernt man durch eine 3-jährige Ausbildung (siehe "),_c('b-link',{staticClass:"bold",attrs:{"to":"/karriere/mfa/ausbildung-berufsbild"}},[_vm._v("Ausbildung zur MFA")]),_vm._v("). Alternativ kannst Du auch eine Umschulung zur MFA absolvieren, wenn Du schon einen Ausbildungsabschluss in einem anderen Beruf hast. Die Umschulung dauert ca. 24-30 Monate. ")],1),_c('p',[_vm._v(" Es gibt unterschiedliche Modelle, um die Umschulung zur MFA / ZFA zu machen. Oft findet die Umschulung rein schulisch statt. Hier empfiehlt es sich ein Praktikum in einer Arztpraxis zu leisten, um bestimmte Tätigkeiten, z.B. Blutabnehmen, EKG schreiben, auch einmal praktisch ausgeführt zu haben. Oder Du machst die Umschulung dual. D.h. Dir werden praktische Inhalte in der Arztpraxis beigebracht und theoretische Inhalte an 1-2 Schultagen pro Woche vermittelt. Bei der rein schulischen Variante liegt die Umschulungsdauer meist bei 24 Monaten. Du erhältst bei der schulischen Umschulung kein Ausbildungsgeld. Das Arbeitsamt unterstützt in vielen Fällen bei einer Zweitausbildung. ")]),_c('p',[_vm._v(" Viele Anbieter der Umschulung zur MFA & ZFA bieten dies auch in Teilzeitmodellen an. ")]),_c('p',[_vm._v(" Auch im Rahmen der Umschulung findet die offizielle Abschlussprüfung mit einem praktischen und schriftlichen Teil statt. Bei bestandener Prüfung vor der Ärztekammer hast Du den Berufsabschluss zur MFA / ZFA erworben. Mit dem anerkannten Abschluss zur Medizinischen Fachangestellten | Zahnmedizinischen Fachangestellten hast Du sehr gute Chancen auf dem "),_c('b-link',{staticClass:"bold",attrs:{"to":"/stellenangebote"}},[_vm._v("Arbeitsmarkt")]),_vm._v(" eine Anstellung zu finden. ")],1),_c('h2',{staticClass:"display-4 bold",attrs:{"id":"quereinstieg"}},[_vm._v(" Quereinstieg als MFA & ZFA ")]),_c('p',[_vm._v(" Für viele ist der Quereinstieg in einer Arztpraxis interessant. Wegen des immer größer werdenden Problems des Fachkräftemangels sind viele Praxen sehr offen für Bewerber aus anderen Branchen. Besonders Kandidaten aus dem Hotel- und Gastgewerbe sind beliebt, da diese Berufsgruppen bereits Erfahrung und die Motivation zur hohen Serviceorientiertheit mitbringen. ")]),_c('p',[_vm._v(" Meist bietet sich hier die Gelegenheit für Quereinsteiger am Praxisempfang bzw. der Rezeption zu starten und verwaltungstechnische und organisatorische Aufgaben, wie Patientenannahme, Terminvergabe, Telefonate, etc. auszuführen. Bei entsprechendem Interesse bieten viele Praxen die Chance auch in medizinische Tätigkeiten im Bereich der Diagnostik und Therapie eingearbeitet zu werden. ")]),_c('p',[_vm._v(" Mittlerweile gibt es "),_c('b-link',{staticClass:"bold",attrs:{"to":"/fortbildungskatalog"}},[_vm._v("Fortbildungsanbieter")]),_vm._v(" die berufsbegleitende Kurse für MFA & ZFA Quereinsteiger anbieten, um die notwendigen theoretischen Inhalte von Praxisorganisation, Abrechnung, Qualitätsmanagement und allgemeinen Grundlagen aus dem Gesundheitswesen zu erwerben und sich schneller im neuen Job zurecht zu finden. ")],1),_c('SearchWidgetJob',{staticClass:"mt-5"}),_c('JobListTeaser',{staticClass:"mt-4"})],1),_c('ScrollTopButton'),_c('Head',{attrs:{"title":"Fort- und Weiterbildung für MFA | ZFA | ArzthelferInnen","desc":"Fortbildungen und Weiterbildungen für Zahnmedizinische | Medizinische Fachangestellte (ZFA & MFA) im Gesundheitswesen || Überblick | Finanzierung | Studium","img":"/img/Weiterbildung_Weiterbildungsdschungel_ugahcj_c_scale,w_1200.jpg","script":_vm.snippet}})],1)}
